import { DATE_FORMAT_STRING } from "@/config";
import * as fertilizationService from "@/service/fertilization";
import { format } from "date-fns";

const state = () => ({
    fertilization: null,
    fertilizationLines: [],
    maturityList: [],
    stageList: [],
    gradeList: [],
    primaryCycles: [],
    cycleTypes: []
});

const actions = {
    async getFertilization({ commit, state }, cycleId) {
        try {
            const data = await fertilizationService.getFertilizationByCycleId(
                cycleId
            );
            commit("SET_FERTILIZATION", data);
            return Promise.resolve(data);
        } catch (err) {
            state.fertilizationLines = [];
            return Promise.reject();
        }
    },
    async createFertlization({ state }, payload) {
        try {
            const data = await fertilizationService.createFertlization(payload);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async updateFertlization({ dispatch, state }, payload) {
        try {
            const data = await fertilizationService.updateFertlization(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },
    async getFertilizationLines({ state }, fertId) {
        try {
            const data = await fertilizationService.getFertilizationLine(
                fertId
            );
            state.fertilizationLines = data;
            return Promise.resolve();
        } catch (err) {
            state.fertilizationLines = [];
            return Promise.reject();
        }
    },
    async createFertilizationLine({ state, dispatch }, payload) {
        try {
            const data = await fertilizationService.createFertilizationLine(
                payload
            );
            await dispatch("getFertilizationLines", state.fertilization.fertId);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async deleteFertilizationLine({ state, dispatch }, payload) {
        try {
            const data = await fertilizationService.deleteFertilizationLine(
                payload
            );
            return dispatch(
                "getFertilizationLines",
                state.fertilization.fertId
            );
        } catch (err) {
            return Promise.reject();
        }
    },
    async copyFertilizationLine({ state, dispatch }, fertId) {
        try {
            const data = await fertilizationService.copyFertilizationLine(
                state.fertilization.fertId
            );
            return dispatch(
                "getFertilizationLines",
                state.fertilization.fertId
            );
        } catch (err) {
            return Promise.reject();
        }
    },
    async getOccyteEmbryoDialogOptions({ state }) {
        try {
            const {
                maturityList,
                stageList,
                gradeList
            } = await fertilizationService.getOocyteEmbryoOptions();
            state.maturityList = maturityList;
            state.stageList = stageList;
            state.gradeList = gradeList;
        } catch (err) {}
    },
    async saveFertlizationEmbryoDetail({ state, dispatch }, payload) {
        try {
            const data = await fertilizationService.saveFertlizationEmbryoDetail(
                payload
            );
            await dispatch("getFertilizationLines", state.fertilization.fertId);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getFertlizationEmbryoDetail({ state }, oocId) {
        try {
            const data = await fertilizationService.getFertlizationEmbryoDetail(
                oocId
            );
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async saveFertilizaionFollAspiration({ state }, payload) {
        try {
            const data = await fertilizationService.saveFertilizaionFollAspiration(
                payload
            );
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async getPrimaryCycles({ state, rootState }) {
        let cycleDate = format(
            new Date(rootState.artCycle.selectedCycle.cycleDate),
            DATE_FORMAT_STRING
        );
        let cycles = await fertilizationService.getPrimaryCycles(
            rootState.artCycle.selectedCycle.patientId,
            cycleDate
        );

        state.primaryCycles = cycles.map((item) => ({
            ...item,
            label: `IVF Cycle ID ${item.cycleId} (${item.cycleDate})`
        }));
    },
    async getCycleTypes({ state }) {
        state.cycleTypes = await fertilizationService.getCycleTypes();
    }
};

const mutations = {
    SET_FERTILIZATION(state, payload) {
        state.fertilization = payload;
    },
    EMPTY_FERTILIZAION_LINE(state) {
        state.fertilizationLines = [];
    }
};
const getters = {};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
